import React from 'react'
import styled from 'styled-components'

let base_font_size = 32;

export const H2 = styled.h2`
font-size: 35px;
font-weight: 700;
line-height: 35px;
text-align: ${props => props.align || 'left'};
`
export const H4 = styled.h4`
font-size: 22px;
font-weight: 700;
line-height: 35px;
text-align: ${props => props.align || 'left'};
`